import { CqsSearchQuery } from "@rp/common/api-types"
import { isRpqlLogicalGroup } from "@rp/common/api-types"
import { DEFAULT_EXPRESSION } from "./defaultExpression"

export const isCqsQueryNotEmpty = ({ expression = DEFAULT_EXPRESSION }: CqsSearchQuery) => {
  return isRpqlLogicalGroup(expression) && expression.value.length > 0
}
